<template>
  <div class="todo">
    <NavbarAdminComponent v-if="esAdmin()" />
    <NavbarComponent v-else></NavbarComponent>
    <div class="container mt-4 mb-2">
      <h1 class="text-center">Bienvenid@, {{ nombreNegocio }}</h1>
      <div v-if="esAdmin()" class="container ">
        <div class="col-md-6 mt-2">
          <router-link class="btn btn-menu" to="/u/negocios">Listar Negocios</router-link>
        </div>

      </div>
      <div v-else class="text-center">
        <div class="row g-3 div-forms border">
          <div class="col-md-6">
            <h2 class="titulo-div-forms mb-2">Perfil</h2>
            <router-link class="btn btn-menu" to='/u/modificar'>Ver Informacin</router-link>
          </div>
        </div>
        <div class="row g-3 div-forms border mt-2">
          <div class="col-md-6">
            <h2 class="titulo-div-forms mb-2">Productos</h2>
            <router-link class="btn btn-menu" to="/u/nuevoProducto">Nuevo Producto</router-link><br>
            <router-link class="btn btn-menu" to='/u/productos'>Listar Productos</router-link>
          </div>
        </div>
        <div class="row g-3 div-forms border mt-2">
          <div class="col-md-6">
            <h2 class="titulo-div-forms mb-2">
              Tu link
            </h2>
            <a :href="'https://puestito.online/' + nombreUsuario" target="_blank">https://puestito.online/{{
              nombreUsuario }}</a><br>
          </div>
        </div>
        <div v-if="fechaVence !== '2100'" class="div-forms mt-2">
          <h2 class="titulo-div-forms mb-2">Tu cdigo QR</h2>
          <div ref="contenido" class="position-relative">
            <img class="portada" :src="negocio.portada" alt="">
            <div class="p-4 frente">
              <img class="logo" :src="negocio.imagen" width="100" alt="">
              <h3>Consult el men</h3>
              <a style="color:black;text-decoration: none;" ref="qrcode"
                :href="'https://puestito.online/' + nombreUsuario" target="_blank"></a><br>
            </div>
          </div>
          <div class="col-md-6">

            <button @click="descargarQR" class="btn btn-menu margenbtn">Descargar QR</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from 'qrcode-generator';
import NavbarComponent from "./NavbarComponent.vue";
import NavbarAdminComponent from "./NavbarAdminComponent.vue";
import { RouterLink } from "vue-router";
import { saveAs } from 'file-saver'; // Importa la funcin saveAs de la biblioteca file-saver
import html2canvas from 'html2canvas';
import axios from 'axios';
export default {
  data() {
    return {
      nombreUsuario: '',
      nombreNegocio: '',
      fechaVence: '',
      negocio: '',
    }
  },
  components: {
    NavbarComponent,
    NavbarAdminComponent,
    "router-link": RouterLink,
  },
  mounted() {
    this.leerUsuario();
    if (this.nombreUsuario != 'admin') {
      this.generarQR();
    }
  },
  created() {
    // Realiza una solicitud HTTP para obtener los informes desde el servidor
    this.obtenerInformacionNegocio();
  },
  methods: {
    async obtenerInformacionNegocio() {
      try {
        this.obteniendoInfo = true;
        // Realiza una solicitud HTTP GET para obtener los informes desde el servidor
        const response = await axios.get(`/miNegocio?usuario=${localStorage.getItem('usuario')}`);
        // Actualiza la lista de informes con los datos recibidos
        this.negocio = response.data;
      } catch (error) {
        console.error("Error al cargar los productos:", error);
      } finally {
        this.obteniendoInfo = false;
      }

    },
    generarQR() {
      // Obtn el nombre de usuario desde el almacenamiento local
      const nombreUsuario = localStorage.getItem("nombre");

      // Verifica si el nombre de usuario existe antes de generar el cdigo QR
      if (nombreUsuario) {
        // Crea una instancia de la clase QRCode
        const qr = QRCode(0, 'L');

        // Define el tamao del mdulo del cdigo QR
        const tamao = 9; // Ajusta el tamao segn tus necesidades
        qr.size = tamao;

        // Define el contenido del cdigo QR (puedes ajustar esto segn tus necesidades)
        const url = 'https://puestito.online/' + nombreUsuario;
        qr.addData(url);
        qr.make();

        // Renderiza el cdigo QR en el div con ref="qrcode"
        this.renderQRCode(qr.createImgTag(tamao, 0));
      }
    },
    renderQRCode(qrCodeImageTag) {
      // Renderiza la imagen del cdigo QR en el div con ref="qrcode"
      // Obtener el elemento al que deseas agregar la clase
      const qrcodeElement = this.$refs.qrcode;

      // Agregar la clase al elemento
      qrcodeElement.innerHTML = qrCodeImageTag;
      qrcodeElement.innerHTML += '<br>puestito.online/' + this.nombreUsuario;

    },
    leerUsuario() {
      this.nombreUsuario = localStorage.getItem("usuario");
      this.nombreNegocio = localStorage.getItem("nombre");
      this.fechaVence = localStorage.getItem("ao");
    },
    esAdmin() {
      return (localStorage.getItem("usuario") == "admin");
    },
    descargarQR() {
      const contenidoDiv = this.$refs.contenido;
      html2canvas(contenidoDiv).then(canvas => {
        canvas.toBlob(blob => {
          saveAs(blob, 'contenido-' + this.nombreUsuario + '.png');
        });
      });
    },
  }
};
</script>

<style scoped>
.position-relative {
  margin: 0 10% 0 10%;
  background: none;
}

.frente {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.portada {
  width: 60%;
  opacity: 0.3;
  filter: blur(3px);
  position: relative;
}

.logo {
  border-radius: 100%;
}

.margenbtn {
  margin: 20px auto;
}

.col-md-6 {
  margin: auto;
  display: block !important;

}

.titulo-div-forms {
  margin: auto;
}

.div-forms {
  margin: auto 15vw auto 15vw;
  padding: 20px;
  border-color: black !important;
  border-radius: 5px !important;
  justify-content: center !important;
}

.btn {
  display: block;
  width: 100%;
}


@media screen and (max-width: 992px) {
  .col-md-6 {
    display: block !important;
  }

  .btn {
    display: block !important;
  }

  .div-forms {
    margin: 0px;
  }

  .position-relative {
    margin: 0;
  }

  .portada {
    width: 100%;
  }
}
</style>